@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400&display=swap);
.modal{position:fixed;z-index:5;top:0;left:0;width:100%;height:100%;background-color:#00000052}.modal__container{z-index:10;position:relative;width:80vw;height:90vh;top:50%;left:50%;transform:translate(-50%, -50%);padding:20px;border-radius:4px;box-shadow:0 4px 8px #302e2e12;background-color:#ffffff;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.modal__container-button{align-self:flex-end;cursor:pointer}.modal__container-button svg{fill:#a6a6a6;transition:.3s all}.modal__container-button svg:hover{fill:#000000}.modal__content{padding:0 10px;width:100%;height:90%;display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start}.modal__content-photo{position:relative;align-self:center;max-height:90%;max-width:80%}.modal__content-location{padding:5px 0;font-size:14px;margin-top:16px}.author__info{padding:5px 0;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.author__info-photo{border-radius:50%}.author__info-name{margin-left:5px;font-size:16px}@media screen and (max-width: 1110px){.modal__container{height:80vh}}@media screen and (max-width: 935px){.modal__container{height:72vh}.modal__content{padding:25px 10px}}@media screen and (max-width: 700px){.modal__container{height:62vh}.modal__content{padding:14px 10px}}@media screen and (max-width: 585px){.modal__container{height:52vh}}@media screen and (max-width: 500px){.modal__container{height:42vh}.author__info{display:none}.modal__content-location{display:none}.modal__content{padding:25px 0 0 0}.modal__content-photo{max-width:100%}}

.found{margin-top:24px}.found h1{text-align:center;font-size:36px;margin-bottom:24px}.found__item{margin:10px}.found__item:hover{cursor:pointer}.found__item-info{position:absolute;top:0;left:0;bottom:0;right:0;opacity:0;transition:all 0.3s ease-in-out 0s}.found__item-info:hover::before{content:"";position:absolute;top:0;left:0;bottom:4px;right:0;background:linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.2) 100%),linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%)}.found__item-info-flex{display:flex;align-items:center;margin-right:12px;position:absolute;bottom:16px;left:16px}.found__item-info-flex div{margin-right:12px}.found__item-info-flex div img{border-radius:50%}.found__item-info-flex p{color:#ffffff;font-size:14px}.found__item-info:hover{opacity:1}

.form{position:relative;width:500px;margin:0 0 7px 24px}.form .search__button{display:flex;align-items:center;position:absolute;left:8px;cursor:pointer}.form .search__button svg{fill:#959595;transition:.3s all}.form .search__button svg:hover{fill:#141414}.form .cancel__button{display:flex;align-items:center;position:absolute;right:8px;cursor:pointer}.form .cancel__button svg{fill:#959595;transition:.3s all}.form .cancel__button svg:hover{fill:#141414}.form__content{display:flex;align-items:center}.form__content input{width:100%;padding:12px 38px;border:none;border-radius:7px;font-size:11px;background:#EEEEEE;transition:.3s all}.form__content input:hover{box-shadow:0 0 0 1px #d5d5d5}.form__content input:focus{background:none;box-shadow:0 0 0 1px #c8c8c8}@media screen and (max-width: 655px){.form{width:300px}}@media screen and (max-width: 445px){.form{width:205px;margin:0 0 7px 10px}.form input{font-size:9px}}

.container{max-width:1366px;margin:0 auto;padding:0 15px}.header{position:-webkit-sticky;position:sticky;top:0;z-index:3;border-bottom:1px solid #e6e6e6;background:#fff}.header__content{display:flex;align-items:center;height:62px}.header__left-side{display:flex;align-items:center}

.home{position:relative;margin-bottom:48px}.home__content{width:100%;height:550px}.home__content-info{position:absolute;top:30%;left:35%;z-index:2}.home__content-info h3{font-family:'Bebas Neue', cursive;color:#ffffff;font-size:120px;text-align:center}.home__content-info p{color:#ffffff;font-size:16px}.home__content img{width:100%;height:100%;object-fit:cover}.home__content-item{width:100%;height:100%}.home__content-item span{color:#e0e0e0;cursor:pointer;transition:.3s all}.home__content-item span:hover{color:#ffffff}.home__content-item p{position:absolute;left:7%;bottom:3%;color:#fff;z-index:2}.home__content-item::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:linear-gradient(180deg, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.4) 100%),linear-gradient(180deg, rgba(0,0,0,0.4) 0%, transparent 100%);z-index:2}.home__item{margin:10px}.home__item:hover{cursor:pointer}.home__item-info{position:absolute;top:0;left:0;bottom:0;right:0;opacity:0;transition:all 0.3s ease-in-out 0s}.home__item-info:hover::before{content:"";position:absolute;top:0;left:0;bottom:4px;right:0;background:linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.2) 100%),linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%)}.home__item-info-flex{display:flex;align-items:center;margin-right:12px;position:absolute;bottom:16px;left:16px}.home__item-info-flex div{margin-right:12px}.home__item-info-flex div img{border-radius:50%}.home__item-info-flex p{color:#ffffff;font-size:14px}.home__item-info:hover{opacity:1}@media screen and (max-width: 785px){.home__content-info{left:20%}}@media screen and (max-width: 635px){.home__content-info{left:10%}}@media screen and (max-width: 555px){.home__content-item p{font-size:12px}.home__content-info{left:23%}.home__content-info h3{font-size:65px}.home__content-info p{font-size:10px}}@media screen and (max-width: 440px){.home__content-info{left:10%}.home__content-info p{text-align:center}}

*{padding:0;margin:0;list-style:none;outline:none;font-family:'Montserrat', sans-serif;box-sizing:border-box}html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}body{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;color:#000000}a,span,p,b,h1,h2,h3,h4,h5{color:#000000}h1{font-size:48px}h2{font-weight:600;font-size:28px;line-height:30px}a{text-decoration:none}

